  /* Animated Gradient Background - Emerald & Gold Luxury */
  body::before {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background: linear-gradient(135deg, #0a0e0d 0%, #064e3b 25%, #0f766e 50%, #064e3b 75%, #0a0e0d 100%);
    background-size: 400% 400%;
    animation: gradientShift 15s ease infinite;
  }

  /* Floating Orbs */
  body::after {
    content: '';
    position: fixed;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
    z-index: -1;
    background:
      radial-gradient(circle at 20% 30%, rgba(16, 185, 129, 0.15) 0%, transparent 50%),
      radial-gradient(circle at 80% 70%, rgba(217, 119, 6, 0.12) 0%, transparent 50%),
      radial-gradient(circle at 40% 80%, rgba(15, 118, 110, 0.1) 0%, transparent 50%),
      radial-gradient(circle at 90% 20%, rgba(251, 191, 36, 0.08) 0%, transparent 50%);
    animation: float 20s ease-in-out infinite;
    filter: blur(60px);
  }

  @keyframes gradientShift {
    0%, 100% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
  }

  @keyframes float {
    0%, 100% {
      transform: translate(0, 0) rotate(0deg);
    }
    33% {
      transform: translate(30px, -30px) rotate(120deg);
    }
    66% {
      transform: translate(-20px, 20px) rotate(240deg);
    }
